<template>
  <div class="container">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>{{$t("check.link1")}}</el-breadcrumb-item>
      <el-breadcrumb-item>审核</el-breadcrumb-item>
      <el-breadcrumb-item>翻译审核</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="tableContainer">
      <el-table
        :data="tableData"
        height="100%"
        border
        style="width: 100%" class="table">
        <el-table-column
          prop="task_id"
          label="任务id">
        </el-table-column>
        <el-table-column
          prop="task_name"
          label="任务名">
        </el-table-column>
        <el-table-column
          prop="demand_people"
          label="需求人员">
        </el-table-column>
        <el-table-column
          prop="translation_language"
          label="翻译语言">
        </el-table-column>
        <el-table-column
          prop="urgency_degree"
          label="紧急程度">
        </el-table-column>
        <el-table-column
          label="任务详情">
          <template slot-scope="scope">
            <el-button @click="openTaskInfo(scope.row)" type="text" size="small">查看</el-button>
          </template>
        </el-table-column>
        <el-table-column
          prop="assign_time"
          label="指派时间">
        </el-table-column>
        <el-table-column
          prop="complete_time"
          label="完成时间">
        </el-table-column>
        <el-table-column
          width="150"
          prop="assign"
          label="指派">
          <template slot-scope="scope">
            <el-select v-model="scope.row.assign" placeholder="请选择翻译官" size="small" @change="assignCompleter(scope.row.assign,scope.row)">
              <el-option
                v-for="item in userOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column
          width="180"
          label="审核">
          <template slot-scope="scope">
            <el-button type="success" round size="small" v-if="scope.row.status != '1'" @click="changeStatus(scope.row,1)">通过</el-button>
            <el-button type="warning" round size="small" v-if="scope.row.status != '3'" @click="changeStatus(scope.row,3)">未通过</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  const language = {
    'CN': '简体中文',
    'HK': '繁体中文-香港',
    'TW': '繁体中文-台湾',
    'EN': '英语',
    'ID': '印尼语',
    'TH': '泰语',
    'MY': '马来语',
    'UA': '阿拉伯语'
  }
  const degree = ["低","中","高"];
  const status = ['新增','翻译通过','翻译待审核','翻译不通过','翻译进行中'];
  export default {
    name: "check",
    data() {
        return {
          tableData: [
            /*{
              task_id: 1000,
              task_name: '设计海报',
              demand_people: '张三',
              translation_language: '泰语',
              urgency_degree: '紧急',
              translate_people: "李四",
              designer: "王五",
              status: "进行中",
              assign_time: '2018-05-23',
              complete_time: '2018-05-23',
            }*/
          ],
          userOptions: [],
          totalSize: 0,
          currentPage: 1,
          pageSize: 100
        }
    },
    methods: {
      assignCompleter(uid,row) {
        let url = '/v1/translations/fix-users';
        let param = {
          id: row.task_id,
          fix_uid: uid
        };
        let that = this;
        this.$axios.post(url,param).then((res) => {
          if(res.data.code == 200){
            that.$message({
              type: 'success',
              message: '指定成功!'
            });
          }else{
            that.$message({
              type: 'error',
              message: '指定失败!'
            });
          }
        }).catch((err) => {
          that.$message({
            type: 'error',
            message: '指定失败!'
          });
        })
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      initTable() {
        let initList = [];
        let url = "/v1/translations?page="+this.currentPage+"&pagesize="+this.pageSize;
        let that = this;
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){
            let tasksList = res.data.data.list;
            that.totalSize = parseInt(res.data.data.page_total);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.task_id = task.id;
              tempTask.task_name = task.title;
              tempTask.demand_people = task.username;
              tempTask.translation_language = language[task.t_language];
              tempTask.urgency_degree = degree[task.level];
              tempTask.assign = task.fix_name;
              tempTask.designer = task.designer;
              tempTask.status = status[task.status];
              tempTask.assign_time = task.fix_time;
              tempTask.complete_time = task.final_time;
              initList.push(tempTask);
            })
          }
          that.tableData = initList;
        }).catch((err) => {})

        let translaterUrl = '/v1/translations/user-name';
        this.$axios.get(translaterUrl).then((res) => {
          if(res.data.code == 200){
            let arr = res.data.data;
            arr.forEach((user) => {
              let tempUser = {};
              tempUser.label = user.username;
              tempUser.value = user.id;
              that.userOptions.push(tempUser)
            })
          }
        }).catch((err) => {
          that.$message({
            type: 'error',
            message: '获取翻译人员失败!'
          });
        })
      },
      openTaskInfo(row) {
        this.$router.push({path:'/taskManage/checkTaskInfo',query: row});
      },
      changeStatus(row,status) {
        let url = '/v1/translations/ajax-status';
        let param = {};
        param.update_by = 1;
        param.id = row.task_id;
        param.status = status;
        if(status == 3){
          this.$prompt('请输入原因', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
          }).then(({ value }) => {
            param.unpass_reason = value;
            this.$axios.post(url,param).then((res) => {
                row.status = '3'
            }).catch((err) => {

            })
          });
        }else{
          this.$axios.post(url,param).then((res) => {
              row.status = '1'
          }).catch((err) => {

          })
        }
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 64px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
      .el-pagination
        text-align right
        margin 8px 0
</style>



